<script>
export default {
  props: {
    days: {
      type: Number,
      default: 0
    }
  },
  computed: {
    styleObj() {
      let percent = Math.max(10, 100 - Math.floor(this.days / 365))
      return {
        transform: 'translate(0, ' + percent +'%)'
      }
    }
  }
}
</script>

