<template>
  <div class="screen screen-welcome">
    <div style="margin: 50px 0">
      <count-circle @click="modal = true" :days="days"></count-circle>
      <v-dialog v-model="modal" persistent width="290px" ref="dialog">
        <v-date-picker scrollable v-model="date">
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="modal = false">Cancel</v-btn>
          <v-btn text color="primary" @click="setDate(date)">OK</v-btn>
        </v-date-picker>
      </v-dialog>
    </div>
    <div class="text-center">
      <user-circle :user="user1" name="user1"></user-circle>
      <user-circle :user="user2" name="user2"></user-circle>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import moment from 'moment'
import CountCircle from '@/components/CountCircle'
import UserCircle from '@/components/UserCircle'

export default {
  name: 'WelcomeScreen',
  components: {
    CountCircle,
    UserCircle
  },
  data() {
    return {
      modal: false,
      date: ''
    }
  },
  created() {
    this.date = moment(this.start_day).format('YYYY-MM-DD')
  },
  computed: {
    ...mapState({
      start_day: state => state.start_day,
      user1: state => state.user1,
      user2: state => state.user2
    }),
    days() {
      return moment().diff(moment(this.start_day), 'days') + 1
    }
  },
  methods: {
    setDate(val) {
      this.$store.commit('SET_DATE', val)
      this.modal = false
    }
  }
}
</script>
