<template>
<v-app>
  <component :is="screenName"></component>
</v-app>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data() {
    return {
      startTime: Date.now(),
      screen: 'splash'
    }
  },
  computed: {
    ...mapState({
      hasPassCode: state => Boolean(state.pass_code)
    }),
    screenName() {
      return this.screen + '-screen'
    }
  },
  methods: {
    setNextScreen() {
      if (this.hasPassCode) {
        this.screen = 'locked'
      } else {
        this.screen = 'welcome'
      }
    }
  },
  async mounted() {
    this.startTime = Date.now()
    await this.$store.restored
    setTimeout(() => {
      this.setNextScreen()
    }, Math.max(0, Date.now() - this.startTime + 1000))
  }
}
</script>